'use client'

import { useRef, useCallback } from 'react'
import type { Editor } from '@tiptap/react'
import type { RefObject } from 'react'
import { Link2, Image, RemoveFormatting } from 'lucide-react'
import { ToolbarButton, SkeletonButton } from './ToolbarButton'
import { clearMarkdownFormatting } from '../../../lib/markdown-helpers'

interface MediaButtonsProps {
  editor?: Editor | null
  textareaRef?: RefObject<HTMLTextAreaElement | null>
  markdown?: string
  onMarkdownChange?: (markdown: string) => void
  aiGenerating?: boolean
  loading?: boolean
  apiBasePath?: string
}

export function MediaButtons({ editor: editorProp, textareaRef, markdown, onMarkdownChange, aiGenerating, loading, apiBasePath = '/api/cms' }: MediaButtonsProps) {
  // Cast editor to any to support StarterKit commands
  const editor = editorProp as any
  const fileInputRef = useRef<HTMLInputElement>(null)
  const isMarkdownMode = !editor && textareaRef && markdown !== undefined && onMarkdownChange

  const handleLinkClick = useCallback(() => {
    if (editor) {
      if (editor.isActive('link')) {
        editor.chain().focus().unsetLink().run()
        return
      }
      const url = window.prompt('Enter URL:')
      if (url) {
        editor.chain().focus().extendMarkRange('link').setLink({ href: url }).run()
      }
    } else if (isMarkdownMode) {
      const url = window.prompt('Enter URL:')
      if (url && textareaRef?.current) {
        const textarea = textareaRef.current
        const start = textarea.selectionStart
        const end = textarea.selectionEnd
        const selected = markdown!.substring(start, end) || 'link text'
        const newText = markdown!.substring(0, start) + `[${selected}](${url})` + markdown!.substring(end)
        onMarkdownChange!(newText)
        requestAnimationFrame(() => textarea.focus())
      }
    }
  }, [editor, isMarkdownMode, textareaRef, markdown, onMarkdownChange])

  const handleImageUpload = useCallback(async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (!file) return

    // Client-side validation
    const maxSize = 4 * 1024 * 1024 // 4MB
    if (file.size > maxSize) {
      alert(`Image too large (${(file.size / 1024 / 1024).toFixed(1)}MB). Maximum size is 4MB.`)
      e.target.value = ''
      return
    }

    const validTypes = ['image/jpeg', 'image/png', 'image/gif', 'image/webp']
    if (!validTypes.includes(file.type)) {
      alert(`Invalid file type: ${file.type}. Supported types: JPEG, PNG, GIF, WebP.`)
      e.target.value = ''
      return
    }

    const formData = new FormData()
    formData.append('image', file)

    try {
      const res = await fetch(`${apiBasePath}/upload`, {
        method: 'POST',
        body: formData,
      })
      
      const data = await res.json()
      
      if (!res.ok) {
        alert(data.error || 'Failed to upload image')
        e.target.value = ''
        return
      }

      // Handle both { url: ... } and { data: { url: ... } } response formats
      const url = data.url || data.data?.url
      if (url) {
        if (editor) {
          editor.chain().focus().setImage({ src: url }).run()
        } else if (isMarkdownMode && textareaRef?.current) {
          const textarea = textareaRef.current
          const start = textarea.selectionStart
          const newText = markdown!.substring(0, start) + `![image](${url})` + markdown!.substring(start)
          onMarkdownChange!(newText)
          requestAnimationFrame(() => textarea.focus())
        }
      } else {
        alert('Upload succeeded but no URL returned')
      }
    } catch (err) {
      alert('Failed to upload image. Please try again.')
      console.error('Image upload error:', err)
    }

    e.target.value = ''
  }, [editor, isMarkdownMode, textareaRef, markdown, onMarkdownChange, apiBasePath])

  const handleClearFormatting = useCallback(() => {
    if (editor) {
      editor.chain().focus().unsetAllMarks().clearNodes().run()
    } else if (isMarkdownMode && textareaRef?.current && markdown && onMarkdownChange) {
      clearMarkdownFormatting(textareaRef.current, markdown, onMarkdownChange)
    }
  }, [editor, isMarkdownMode, textareaRef, markdown, onMarkdownChange])

  // Skeleton state - render placeholders matching actual button layout
  if (loading) {
    return (
      <>
        {/* Link, Image, Clear formatting */}
        <SkeletonButton />
        <SkeletonButton />
        <SkeletonButton />
      </>
    )
  }

  return (
    <>
      <ToolbarButton
        onClick={handleLinkClick}
        active={editor?.isActive('link')}
        disabled={aiGenerating}
        title="Insert link"
      >
        <Link2 className="w-4 h-4" />
      </ToolbarButton>
      <ToolbarButton onClick={() => fileInputRef.current?.click()} disabled={aiGenerating} title="Insert image">
        <Image className="w-4 h-4" />
      </ToolbarButton>
      <input
        ref={fileInputRef}
        type="file"
        accept="image/*"
        onChange={handleImageUpload}
        className="hidden"
      />
      <ToolbarButton onClick={handleClearFormatting} disabled={aiGenerating} title="Clear formatting">
        <RemoveFormatting className="w-4 h-4" />
      </ToolbarButton>
    </>
  )
}
